/**
 * Styles
 *
 * The following styles get applied to the WordPress Admin
 * area.
 */
#wholesome-boilerplate-settings .components-placeholder {
  background: #f1f1f1; }

#wholesome-boilerplate-settings .wholesome-boilerplate-header {
  background-color: #ffffff;
  box-shadow: 0 1px 0 rgba(213, 213, 213, 0.5), 0 1px 2px #eeeeee;
  margin-left: -2em;
  padding: 20px 10px; }
  #wholesome-boilerplate-settings .wholesome-boilerplate-header .wholesome-boilerplate-container {
    margin: 0 auto;
    max-width: 750px; }
    #wholesome-boilerplate-settings .wholesome-boilerplate-header .wholesome-boilerplate-container .wholesome-boilerplate-logo {
      align-items: center;
      display: flex;
      justify-content: center; }

#wholesome-boilerplate-settings .wholesome-boilerplate-main {
  margin-left: auto;
  margin-right: auto;
  max-width: 750px; }
  #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body {
    background: #ffffff;
    border: 1px solid #e2e4e7;
    margin: 25px 0; }
    #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row {
      margin: 0;
      padding: 10px; }
      #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control .components-base-control__field {
        align-items: center;
        display: flex;
        margin-bottom: 10px; }
      #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field {
        width: 100%; }
        #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field {
          display: block; }
          #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field label {
            font-weight: 600;
            margin-bottom: 10px; }
          #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field input,
          #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field select {
            border: 1px solid #b7c9d9;
            border-radius: 3px;
            box-shadow: none;
            color: #444444;
            height: 40px;
            margin: 0 0 10px 0;
            max-width: inherit;
            padding: 9px 15px;
            width: 100%; }
            #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field input:disabled,
            #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field select:disabled {
              background: #f6f6f6;
              border-color: #eeeeee;
              color: #bbbbbb; }
            #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field input:focus,
            #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field select:focus {
              border-color: #00a0d2;
              box-shadow: 0 0 0 1px #00a0d2;
              outline: 2px solid transparent;
              outline-offset: -2px; }
          #wholesome-boilerplate-settings .wholesome-boilerplate-main .components-panel__body .components-panel__row .components-base-control.wholesome-boilerplate-field .components-base-control__field select {
            padding-top: 6px; }
